require("dotenv").config()
const axios = require('axios');
const pinataSDK = require('@pinata/sdk');
const pinata = pinataSDK(process.env.PINATA_API_KEY, process.env.PINATA_API_SECRET);

import 'tw-elements';

retrieveEvents()

const webcamCamera = document.querySelector("#webcam-camera")
const webcamSnapShot = document.querySelector("#webcam-snapshot")
const takeSnapShotButton = document.querySelector("#take-photo")
const openCameraButton = document.querySelector("#open-camera")
const eventName = document.querySelector("#event-name")
const dateUploaded = document.querySelector("#date-uploaded")
const eventDescription = document.querySelector("#event-description")
const eventLocation = document.querySelector("#event-location")
const uploadEventButton = document.querySelector("#upload-event-details")
const uploadedEventsList = document.querySelector("#uploaded-events")
const modalSection = document.querySelector("#modal-section")
const switchCameraButton = document.querySelector("#switch-camera")

const modText = (x) => (x.length > 20 ? x.substring(0, 20) + "..." : x);

let snapshots = []
let uploadedEvents = []
let rearCamera

takeSnapShotButton.addEventListener("click", takeSnapShot)
openCameraButton.addEventListener("click", init)
switchCameraButton.addEventListener("click", () => {
	rearCamera == true ? init() : init("rear")
	// if(rearCamera == true) {
	// 	init()
	// } else {
	// 	init("rear")
	// }
})
uploadEventButton.addEventListener("click", uploadEvent)

async function uploadEvent() {
    if (eventName.value.toString().trim() != "" &&
        eventDescription.value.toString().trim() != "" &&
        eventLocation.value.toString().trim() != "" &&
        dateUploaded.value.toString().trim() != "" &&
        snapshots.length > 0
    ) {
        const newEvent = {
            name: eventName.value.toString().trim(),
            description: eventDescription.value.toString().trim(),
            location: eventLocation.value.toString().trim(),
            date: new Date(dateUploaded.value.toString().trim().split("-").join(",")),
            images: snapshots,
            id: Date.now()
        }

        const options = {
            pinataMetadata: {
                name: "customEvent",
            },
            pinataOptions: {
                cidVersion: 1
            }
        };

        pinata.pinJSONToIPFS(newEvent, options).then((result) => {
            showModal("Success")
            resetForm()
            retrieveEvents()
        }).catch((err) => {
            showModal("An error occured, retry")
        });
    } else {
    	showModal("All fields & at least one image is required")
    }
}

function resetForm() {
	eventName.value = null
    eventDescription.value = null
    eventLocation.value = null
    dateUploaded.value = null
    snapshots = []
}

function showModal(info) {
	modalSection.insertAdjacentHTML(
        "afterbegin",
        `
			<div class="modal fixed top-2 left-0 w-full h-full z-20 outline-none overflow-x-hidden overflow-y-auto sm:top-3" role="dialog" id="modal-toggle">
			    <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable relative w-2/3 m-auto pointer-events-none">
			    	<div class="modal-content border-2 shadow-xl relative flex flex-col w-full pointer-events-auto bg-white bg-clip-padding rounded-md outline-none text-current">
			    		<div class="modal-header flex flex-shrink-0 items-center justify-between p-4 border-b border-gray-200 rounded-t-md">
			    			<h5 class="text-xl font-medium leading-normal text-gray-800">
			    				<h5 class="font-bold text-sm">Info</h5>
			    			</h5>
			    			<button type="button"
			    				class="btn-close box-content w-3 h-3 p-1 text-red border-none rounded-none opacity-50 focus:shadow-none focus:outline-none focus:opacity-100 hover:text-red hover:opacity-80 hover:no-underline"
			    				id="close-modal"
			    				onclick="document.querySelector('#modal-section').innerHTML = ''"
			    			></button>
			    		</div>
			    		<div class="modal-body relative p-4 flex flex-col justify-center items-center">
				        	<p class="font-normal text-sm flex flex-col justify-center items-center px-2 space-y-1">
				        		${info}
				        	</p>
						</div>
					</div>
				</div>
			</div>
		`
    )

}

function retrieveEvents() {
    const metadataFilter = {
        name: 'customEvent',
    };

    const filters = {
        status: 'pinned',
        pageLimit: 10,
        pageOffset: 0,
        metadata: metadataFilter
    };
    pinata.pinList(filters).then((result) => {
    	uploadedEventsList.classList.remove("hidden");
        const IPFSHash = result.rows.map(hash => {
            return hash.ipfs_pin_hash
        })
        uploadedEvents = []
        IPFSHash.forEach(hash => {
            axios.get(`https://gateway.pinata.cloud/ipfs/${hash}`).then(res => {
                uploadedEvents.push(res.data);
                loadUploadedEvents();
            }).catch(e => {})
        })
    }).catch((err) => {});
}

function loadUploadedEvents() {
    uploadedEventsList.innerHTML = ""
    if (uploadedEvents.length > 0) {
        uploadedEvents.forEach(event => {
            event.id ?
                uploadedEventsList.insertAdjacentHTML(
                    "afterbegin",
                    `
						<li
							class="flex justify-between items-center w-full py-2 px-4 hover:bg-gray-100"
						>
							<span class="text-left">${modText(event.name)}</span>
							<span class="text-left">${(modText(new Date(event.date)).toDateString())}</span>
							<span>
								<button
									type="button"
									class="event-info mx-auto px-2 py-0.5 text-white bg-emerald-700 cursor-pointer font-normal text-xs leading-tight rounded-full shadow-md hover:bg-emerald-900 hover:shadow-lg focus:shadow-lg focus:outline-none focus:ring-0 transition duration-150 ease-in-out"
									id="${event.id}"
								>i
								</button>
							</span>
						</li>
					`
                ) : ""
            document.querySelectorAll(".event-info").forEach(event => {
                event.addEventListener("click", showEventInfo)
            })
        })
    }
}

function showEventInfo(e) {
    modalSection.innerHTML = ''
    const a = uploadedEvents.filter(x => {
        return x.id == e.target.id
    })
    modalSection.insertAdjacentHTML(
        "afterbegin",
        `
			<div class="modal fixed top-2 left-0 w-full h-full z-20 outline-none overflow-x-hidden overflow-y-auto sm:top-3" role="dialog" id="modal-toggle">
			    <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable relative w-2/3 m-auto pointer-events-none">
			    	<div class="modal-content border-2 shadow-xl relative flex flex-col w-full pointer-events-auto bg-white bg-clip-padding rounded-md outline-none text-current">
			    		<div class="modal-header flex flex-shrink-0 items-center justify-between p-4 border-b border-gray-200 rounded-t-md">
			    			<h5 class="text-xl font-medium leading-normal text-gray-800">
			    				<h5 class="font-bold text-sm">${a[0].name}</h5>
			    			</h5>
			    			<button type="button"
			    				class="btn-close box-content w-3 h-3 p-1 text-red border-none rounded-none opacity-50 focus:shadow-none focus:outline-none focus:opacity-100 hover:text-red hover:opacity-80 hover:no-underline"
			    				id="close-modal"
			    				onclick="document.querySelector('#modal-section').innerHTML = ''"
			    			></button>
			    		</div>
			    		<div class="modal-body relative p-4 flex flex-col justify-center items-center">
				        	<p class="font-normal text-sm flex flex-col justify-center items-center px-2 space-y-1">
				        		<span class="text-left">${a[0].description}</span>
				        		<span class="text-left">${a[0].location}</span>
				        		<span class="text-left">${(new Date(a[0].date)).toDateString()}</span>
				        	</p>
				        	<section class="overflow-hidden text-gray-700 ">
				        		<div class="container px-5 py-2 mx-auto">
				        			<div class="flex flex-wrap -m-1 md:-m-2">
				        				${a[0].images.map(img => 
				        					`<div class="flex flex-wrap w-1/3">
				        						<div class="w-full p-1 md:p-2">
				        							<img src="${img}" class="block mx-auto object-cover object-center w-full h-full rounded-lg"/>
				        						</div>
				        					</div>`
				        				).join("")}
				        			</div>
				        		</div>
							</section>
						</div>
					</div>
				</div>
			</div>
		`
    )
}

function takeSnapShot() {
    Webcam.snap((uri) => {
        snapshots.push(uri)
        webcamSnapShot.innerHTML = ""
        snapshots.forEach(shot => {
            webcamSnapShot.insertAdjacentHTML(
                "afterbegin",
                `
					<div class="flex flex-wrap w-full md:w-1/2 lg:w-1/3">
					    <div class="w-full p-1 md:p-2">
					    	<img
					    		class="block object-cover object-center w-full h-full rounded-lg event-image"
					    		src="${shot}"
					    	>
					    </div>
					  </div>
				`
            )
            document.querySelectorAll(".event-image").forEach(img => {
                img.addEventListener("click", removeSnapShot)
            })
        })
    })
}

function removeSnapShot(e) {
    snapshots = snapshots.filter(shot => {
        return shot != e.target.src
    })
    repopulatePhotos()
}

function repopulatePhotos() {
    webcamSnapShot.innerHTML = ""
    snapshots.forEach(shot => {
        webcamSnapShot.insertAdjacentHTML(
            "afterbegin",
            `
				<div class="flex flex-wrap w-full md:w-1/2 lg:w-1/3">
				    <div class="w-full p-1 md:p-2">
				    	<img
				    		class="block object-cover object-center w-full h-full rounded-lg event-image"
				    		src="${shot}"
				    	>
				    </div>
				  </div>
			`
        )
        document.querySelectorAll(".event-image").forEach(img => {
            img.addEventListener("click", removeSnapShot)
        })
    })
}

function init(direction) {
	Webcam.reset()
    openCameraButton.classList.add("hidden")
    Webcam.set({
        width: 320,
        height: 320,
        image_format: "jpeg",
        jpeg_quality: 100,
    })

    if(direction == "rear") {
    	Webcam.set("constraints", {
    		facingMode: "environment"
    	})
    	rearCamera = true
    } else {
    	rearCamera = false	
    }

    Webcam.attach(webcamCamera)
    takeSnapShotButton.classList.remove("hidden")
    switchCameraButton.classList.remove("hidden")
}
